// 导入 axios 实例
import service from "..";

// 获取页面右上角的电台分类
export function getDjCatelist() {
  return service({
    method: 'GET',
    url: '/dj/catelist'
  })
}

// 获取电台推荐
export function getDjRecommend() {
  return service({
    method: 'GET',
    url: '/dj/recommend'
  })
}

// 获取电台分类推荐
export function getDjRecommendType(type) {
  return service({
    method: 'GET',
    url: `/dj/recommend/type?type=${type}`
  })
}

// 电台详情 
export function getDjDetail(rid) {
  return service({
    method: 'GET',
    url: `/dj/detail?rid=${rid}`
  })
}

// 类别热门电台
export function getDjHot(cateId) {
  return service({
    method: 'GET',
    url: `/dj/radio/hot?cateId=${cateId}`
  })
}
