import { getDjCatelist } from '@/request/api/podcast';
export default {
  data() {
    return {
      // 电台分类
      categories: [],
      // 第一组
      categories1: [],
      // 第二组
      categories2: [],
      // 第三组
      categories3: [],
      // 记录当前位置
      nowPosition: 0
    };
  },

  async mounted() {
    let res = await getDjCatelist(); // console.log(res);

    this.categories = res.data.categories; // console.log(this.categories);
    // 前面插入 “排行榜” 和后面插入 “我要做主播”

    var rankingList = {
      name: '排行榜'
    };
    var anchor = {
      name: '我要做主播'
    };
    this.categories.unshift(rankingList);
    this.categories.push(anchor); // 分开每组

    this.categories1 = this.categories.slice(0, 8);
    this.categories2 = this.categories.slice(8, 16);
    this.categories3 = this.categories.slice(16, this.categories.length); // console.log(this.categories1);
    // console.log(this.categories2);
    // console.log(this.categories3);
    // “我要做主播” 位置调整

    setTimeout(() => {
      // 需要页面渲染完成后才能获取到
      // console.log(this.$refs.categorie[this.$refs.categorie.length - 1]);
      this.$refs.categorie[this.$refs.categorie.length - 1].classList.add('anchor');
    }, 1000);
  },

  methods: {
    // 上一组
    previous() {
      if (this.nowPosition < 0) {
        this.nowPosition += 432;
        this.$refs.categories_box.style.transform = 'translateX(' + this.nowPosition + 'px)';
      } // console.log(this.nowPosition);

    },

    // 下一组
    next() {
      // this.$refs.categories_box.classList.add('nextMove1')
      if (this.nowPosition > -864) {
        this.nowPosition -= 432;
        this.$refs.categories_box.style.transform = 'translateX(' + this.nowPosition + 'px)'; // document.styleSheets[0].insertRule(
        //   `@keyframes next {
        //       from {transform: translateX(` + this.nowPosition + `px);}
        //       to {transform: translateX(` + (this.nowPosition - 432) + `px);}}`, 0);
        // this.$refs.categories_box.style.animation = "next .5s ease forwards";
        // this.nowPosition -= 432
      } // console.log(this.nowPosition);

    }

  }
};